___CSS_LOADER_EXPORT___.push([module.id, ".el-table .warning-row{background:oldlace}.el-table .success-row{background:#f0f9eb}", "",{"version":3,"sources":["webpack://./resources/admin/js/views/check-uniques/index.vue"],"names":[],"mappings":"AAiMA,uBACA,kBACA,CAEA,uBACA,kBACA","sourcesContent":["<template>\n    <section>\n        <div class=\"row\">\n            <div class=\"col-md-6 col-lg-4\">\n                <label>\n                    Система\n                </label>\n                <el-select style=\"width: 100%\" v-model=\"system\" filterable placeholder=\"\"  @change=\"getCheckUniques\">\n                    <el-option\n                        v-for=\"item in systems\"\n                        :key=\"item.id\"\n                        :label=\"item.title\"\n                        :value=\"item.id\"\n                    >\n                    </el-option>\n                </el-select>\n            </div>\n            <div class=\"col-md-6 col-lg-4\">\n                <label>\n                    Статус\n                </label>\n                <el-select style=\"width: 100%\" v-model=\"priceType\" filterable placeholder=\"\"  @change=\"getCheckUniques\">\n                    <el-option\n                        v-for=\"item in priceTypes\"\n                        :key=\"item.value\"\n                        :label=\"item.label\"\n                        :value=\"item.value\"\n                    >\n                    </el-option>\n                </el-select>\n            </div>\n            <div class=\"col-md-6 col-lg-4\">\n                <label>\n                    Формат\n                </label>\n                <el-select style=\"width: 100%\" v-model=\"type\" filterable placeholder=\"\"  @change=\"getCheckUniques\">\n                    <el-option\n                        v-for=\"item in types\"\n                        :key=\"item.value\"\n                        :label=\"item.label\"\n                        :value=\"item.value\"\n                    >\n                    </el-option>\n                </el-select>\n            </div>\n        </div>\n\n\n        <data-tables :data=\"check_uniques\" >\n            <el-table-column\n                label=\"№\"\n            >\n                <template slot-scope=\"scope\">\n                    Запрос № {{scope.row.id}}\n                </template>\n            </el-table-column>\n            <el-table-column\n                label=\"e-mail\"\n                prop=\"email\"\n                sortable\n            >\n            </el-table-column>\n            <el-table-column\n                label=\"Дата время\"\n                prop=\"formatted_date\"\n                sortable\n            >\n            </el-table-column>\n            <el-table-column\n                label=\"Сумма\"\n                prop=\"sum\"\n                sortable\n            >\n            </el-table-column>\n            <el-table-column\n                label=\"Кол-во систем проверки\"\n            >\n                <template slot-scope=\"scope\">\n               <div  v-for=\"(item, index) in scope.row.reports\" style=\"word-break: normal\">\n                   <div>{{item.check_system.title + ', '}}</div>\n                </div>\n                </template>\n            </el-table-column>\n            <el-table-column\n                label=\"Услуги\"\n            >\n                <template slot-scope=\"scope\" >\n                 <span v-for=\"(item, index) in scope.row.services\">\n                              {{item.title + ', '}}\n                        </span>\n                </template>\n            </el-table-column>\n            <el-table-column\n                label=\"Формат\"\n                prop=\"type_title\"\n                ></el-table-column>\n            <el-table-column\n                label=\"Действия\"\n            >\n\n                <template slot-scope=\"scope\">\n                    <el-button\n                        size=\"mini\"\n                        type=\"primary\"\n                        @click=\"handleShow(scope.row.id)\">Подробнее</el-button>\n\n                </template>\n            </el-table-column>\n        </data-tables>\n        <el-dialog\n            title=\"Отчет о проверке\"\n            :visible.sync=\"dialogVisible\"\n            width=\"90%\"\n            :before-close=\"handleClose\">\n           <check-report v-if=\"dialogVisible\" :id=\"currentId\"></check-report>\n        </el-dialog>\n    </section>\n</template>\n<script>\nimport CheckReport from \"./components/report/index\";\n    export default {\n        components: {CheckReport},\n        data() {\n            return {\n                CanAccess:'Просмотр \"Системы проверки\"',\n                check_uniques: [],\n                dialogVisible:false,\n                currentId:null,\n                type:null,\n                priceType:null,\n                system:null,\n                priceTypes: [\n                    {\n                        label:'Бесплатные',\n                        value:0\n                    },\n                    {\n                        label:'Платные',\n                        value:1\n                    },\n\n                ],\n                types: [\n                    {\n                        label:\"Автоматические\",\n                        value:0,\n                    },\n                    {\n                        label:'Ручные',\n                        value:1,\n                    }\n                ],\n                systems: [],\n                tableProps: {\n                    \"row-class-name\": function (row) {\n\n                        if (row.row.status === 0) {\n                            return 'warning-row';\n                        }\n                        return ''\n                    }\n                },\n            }\n        },\n        methods: {\n            getSystems() {\n                axios.get('/api/admin/check-unique/systems')\n                    .then((response) => {\n                        this.systems = response.data;\n                    })\n            },\n            handleClose() {\n                this.dialogVisible = false;\n                this.currentId = false;\n            },\n            handleShow(id) {\n                this.currentId = id;\n                this.dialogVisible = true;\n            },\n            getCheckUniques() {\n                axios.get('/api/admin/check-uniques', {params:{type:this.type, system: this.system, price_type:this.priceType}})\n                    .then((response) => {\n                        this.check_uniques = response.data;\n                    })\n            },\n        },\n        mounted() {\n            this.getCheckUniques();\n            this.getSystems();\n        }\n    }\n</script>\n<style>\n.el-table .warning-row {\n    background: oldlace;\n}\n\n.el-table .success-row {\n    background: #f0f9eb;\n}\n</style>\n"],"sourceRoot":""}]);
